import React, { Fragment, useEffect, useState } from "react";
import { Table, Button } from "reactstrap";

import { getDatabase, ref, onValue, remove } from "firebase/database";
import firebase from "./../firebase";

function DashboardContainer() {
    const [schedules, setSchedules] = useState({});
    const [computerId, setComputerId] = useState(null);

    useEffect(() => {
        const login = JSON.parse(localStorage.getItem('login'));
        const computerId = login.computerId;
        setComputerId(computerId);

        const db = getDatabase(firebase);
        const starCountRef = ref(db, 'schedules/' + computerId);
        onValue(starCountRef, (snapshot) => {
            const data = snapshot.val();
            setSchedules(data);
            console.log(data);
        });
    }, []);

    const handleDelete = (id) => {
        const db = getDatabase(firebase);
        const starCountRef = ref(db, 'schedules/' + id);
        remove(starCountRef);
    }

    return (
        <Fragment>
            <h1 className="text-center ">Danh sch cc my qun l.</h1>

            <Table responsive>
                <thead>
                    <tr>
                        <th>
                            ID
                        </th>
                        <th>
                            My
                        </th>
                        <th>
                            Gi bt u
                        </th>
                        <th>
                            Gi kt thc
                        </th>
                        <th>
                            Thi gian s dng
                        </th>
                        <th>
                            Thi gian ngh
                        </th>
                        <th>
                            Tng thi gian
                        </th>
                        <th>#</th>
                    </tr>
                </thead>

                <tbody>
                    {schedules && <tr key={1} >
                        <th scope="row">
                            1
                        </th>
                        <td>
                            1
                        </td>
                        <td>
                            {schedules.f}
                        </td>
                        <td>
                            {schedules.t}
                        </td>
                        <td>
                            {schedules.d}
                        </td>
                        <td>
                            {schedules.i}
                        </td>
                        <td>
                            {schedules.s}
                        </td>
                        <td>
                            <a className="mr-5 btn btn-warning" href={"/schedule/" + computerId}>Cp nht</a>
                            <Button className="ml-5 btn btn-danger" onClick={() => handleDelete(computerId)}>Xa</Button>
                        </td>
                    </tr>}

                </tbody>
            </Table>
        </Fragment>
    )
}

export default DashboardContainer;